@import "../../sass/mixins"

.sidebar
  &__link
    display: flex
    border-radius: 0.25em
    transition: all 0.2s
    color: #606770
    text-decoration: none
    background-color: #fff
    align-items: center
    justify-content: space-between
    font-family: 'Jost', sans-serif

    &:hover, &_active
      background-color: darken($bg-color, 4)

    &_random
      border: 0
      width: 100%
      text-align: left
      background-color: $primary-color
      color: #fff
      font-family: 'Jost', sans-serif

      &:hover
        background-color: $primary-color-dark

    &_filter
      border: 0
      outline: 0
      padding: 0.25rem 0.75rem
      font-size: 16px
      width: 100%
      text-align: left
      background-color: darken($bg-color, 10)
      color: $text-color

      &:hover
        background-color: darken($bg-color, 25)

    &_active
      color: $secondary-color

    &_dark
      background-color: $primary-color
      color: #fff

      &:hover
        background-color: $primary-color-dark

  &__favorites
    background-color: transparent
    border: 0
    outline: 0

    svg
      fill: transparent
      stroke: #030712
      stroke-width: 1

    &:hover
      svg
        stroke: #030712
        fill: #facc15

    &_active
      svg
        fill: #facc15
        stroke: #facc15
