.rum{background-color:#45362b}.coke{background-color:#370b15}.cranberry-juice{background-color:#ba4242}.orange-juice{background-color:#f7ca05}.crème-de-cassis-liqueur{background-color:#0d0d0b}.peach-schnapps{background-color:#ffdcd6}.vodka{background-color:rgba(191,192,238,.3)}.melon-liqueur{background-color:#00cb00}.lemon-juice{background-color:#fafb73}.lime-juice{background-color:#e5e896}.club-soda{background-color:#e2edeb}.suntory-roku-gin{background-color:rgba(212,241,249,.6666666667)}.simple-syrup{background-color:rgba(187,147,81,.6666666667)}.glass{width:175px;height:450px;box-shadow:0 0 30px 20px rgba(0,0,0,.1);display:flex;padding:1.5rem 1rem 1rem;align-items:flex-end;margin:70px auto;border-radius:0 0 30px 30px;font-size:1.5rem;font-family:"IndieFlower",sans-serif;position:relative}@media screen and (max-width: 1024px){.glass{width:150px;height:375px;font-size:1.2rem;margin:50px 15% 15px auto}}@media screen and (max-width: 768px){.glass{font-size:1rem;padding:1rem .5rem .5rem}}@media screen and (max-width: 639px){.glass{font-size:.8rem;width:100px;height:250px;margin:50px 5% 15px auto}}.glass__tube{background-color:#03adce;position:absolute;width:15%;height:100%;left:60%;bottom:30%;transform:rotate(15deg)}.glass__ice{width:70%;height:27%;position:absolute;border-radius:30%;background-color:rgba(255,255,255,.3);border:1px solid rgba(255,255,255,.3);z-index:1}.glass__ice:nth-of-type(1){bottom:calc(1rem + 2%);right:calc(1rem + 6%);transform:rotate(30deg)}@media screen and (max-width: 768px){.glass__ice:nth-of-type(1){right:1rem;bottom:1rem}}.glass__ice:nth-of-type(2){bottom:calc(1rem + 2% + 27% + 3%);right:calc(1rem + 6%);transform:rotate(-30deg)}@media screen and (max-width: 768px){.glass__ice:nth-of-type(2){right:1rem;bottom:calc(1rem + 27% + 2%)}}.glass__content{width:100%;height:100%;display:flex;flex-direction:column-reverse}.glass__ingredient{width:100%;position:relative;opacity:.98}.glass__ingredient:first-of-type{border-radius:0 0 20px 20px}.glass__ingredient .ingredient__name{right:130%;width:200%;text-transform:capitalize;position:absolute;top:50%;transform:translateY(-50%);text-align:right}@media screen and (max-width: 768px){.glass__ingredient .ingredient__name{width:300%;right:140%}}.glass__ingredient .ingredient__amount{left:130%;width:200%;position:absolute;top:50%;transform:translateY(-50%)}@media screen and (max-width: 1024px){.glass__ingredient .ingredient__amount{display:none}}@media screen and (max-width: 639px){.glass__ingredient .ingredient__amount{width:400%}}@media screen and (max-width: 639px){.glass__ingredient .ingredient__amount{text-align:right;right:300%;top:50%;transform:translateY(-50%);left:auto}}