@import "../../sass/mixins"

.header
  height: 70px
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.1)
  position: sticky
  top: 0
  width: 100%
  background-color: $bg-color
  z-index: 5
  display: flex
  align-items: center
  padding: 0.5rem 1.5rem
  display: flex
  justify-content: space-between
  font-family: 'Jost', sans-serif
  font-weight: bold

  &__logo
    text-decoration: none
    color: #000
    display: flex
    justify-content: center
    align-items: center

    &-img
      width: 30px
      height: 30px
      margin-right: 5px

  &__nav-link
    transition: all 0.2s

    &:hover
      color: $primary-color

  &__auth-buttons
    display: flex
    gap: 15px
    align-items: center

  &__auth-button
    outline: 0
    white-space: nowrap
    background-color: $primary-color
    color: #fff
    border-radius: 5px
    transition: all 0.2s
    border: 0
    font-family: 'Jost', sans-serif
    min-width: 100px
    padding: 0.5rem 1rem

    @include md
      padding: 0.25rem

    &:hover
      background-color: $primary-color-dark

  &__auth-link
    outline: 0
    white-space: nowrap
    color: $text-color
    background-color: transparent
    border-radius: 5px
    transition: all 0.2s
    border: 0
    font-family: "Jost", sans-serif
    min-width: 100px
    padding: 0.5rem 1rem

    @include md
      padding: 0.25rem

    &:hover
      color: $primary-color
      background-color: transparent
